var websock = null;
let rec; //5WebSocket  rec
let isConnect = false; // 
let connectCallBack = null;
let messageCallBack = null;
let closeCallBack = null


let connect = (wsurl, accessToken) => {
	try {
		if (isConnect) {
			return;
		}
		console.log("WebSocket");
		websock = new WebSocket(wsurl);
		websock.onmessage = function (e) {
			let sendInfo = JSON.parse(e.data)
			if (sendInfo.cmd == 0) {
				heartCheck.start()
				// 
				connectCallBack && connectCallBack();
				console.log('WebSocket')
			} else if (sendInfo.cmd == 1) {
				// 
				heartCheck.reset();
			} else {
				// 
				console.log(":", sendInfo);
				messageCallBack && messageCallBack(sendInfo.cmd, sendInfo.data)
			}
		}
		websock.onclose = function (e) {
			console.log('WebSocket')
			isConnect = false; //
			closeCallBack && closeCallBack(e);
		}
		websock.onopen = function () {
			console.log("WebSocket");
			isConnect = true;
			// 
			let loginInfo = {
				cmd: 0,
				data: {
					accessToken: accessToken
				}
			};
			websock.send(JSON.stringify(loginInfo));
		}

		// 
		websock.onerror = function () {
			console.log('WebSocket')
			isConnect = false; //
			reconnect(wsurl, accessToken);
		}
	} catch (e) {
		console.log("");
		reconnect(wsurl, accessToken); //webSocket 
	}
};

//
let reconnect = (wsurl, accessToken) => {
	console.log("");
	if (isConnect) {
		//
		return;
	}
	rec && clearTimeout(rec);
	rec = setTimeout(function () { // 5  
		connect(wsurl, accessToken);
	}, 15000);
};
//
let close = (code) => {
	websock && websock.close(code);
};


//
let heartCheck = {
	timeout: 5000, // 20s
	timeoutObj: null, //
	start: function () {
		if (isConnect) {
			console.log('WebSocket')
			let heartBeat = {
				cmd: 1,
				data: {}
			};
			websock.send(JSON.stringify(heartBeat))
		}
	},

	reset: function () {
		clearTimeout(this.timeoutObj);
		this.timeoutObj = setTimeout(function () {
			heartCheck.start();
		}, this.timeout);

	}
};



// 
let sendMessage = (agentData) => {
	if (websock.readyState === websock.OPEN) {
		// ws
		websock.send(JSON.stringify(agentData))
	} else if (websock.readyState === websock.CONNECTING) {
		//  1s
		setTimeout(function () {
			sendMessage(agentData)
		}, 1000)
	} else {
		//  1s
		setTimeout(function () {
			sendMessage(agentData)
		}, 1000)
	}
}

let onConnect = (callback) => {
	connectCallBack = callback;
}

let onMessage = (callback) => {
	messageCallBack = callback;
}


let onClose = (callback) => {
	closeCallBack = callback;
}
// 
export {
	connect,
	reconnect,
	close,
	sendMessage,
	onConnect,
	onMessage,
	onClose,
}
